'use strict';

/**
* Fill a strided array with pseudorandom numbers drawn from a Bernoulli distribution.
*
* @module @stdlib/random/strided/bernoulli
*
* @example
* import Float64Array from '@stdlib/array/float64';
* import bernoulli from '@stdlib/random/strided/bernoulli';
*
* // Create an array:
* var out = new Float64Array( 10 );
*
* // Fill the array with pseudorandom numbers:
* bernoulli( out.length, [ 0.5 ], 0, out, 1 );
*
* @example
* import Float64Array from '@stdlib/array/float64';
* import bernoulli from '@stdlib/random/strided/bernoulli';
*
* // Create an array:
* var out = new Float64Array( 10 );
*
* // Fill the array with pseudorandom numbers:
* bernoulli.ndarray( out.length, [ 0.5 ], 0, 0, out, 1, 0 );
*/

// MODULES //

import setReadOnly from '@stdlib/utils/define-nonenumerable-read-only-property';
import main from './main.js';
import factory from './factory.js';


// MAIN //

setReadOnly( main, 'factory', factory );


// EXPORTS //

export default main;

// exports: { "factory": "main.factory", "ndarray": "main.ndarray" }
