'use strict';

/*
* When adding modules to the namespace, ensure that they are added in alphabetical order according to module name.
*/

// MODULES //

import setReadOnly from '@stdlib/utils/define-read-only-property';


// MAIN //

/**
* Namespace.
*
* @namespace ns
*/
var ns = {};

/**
* @name binary
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/random/tools/binary}
*/
import binary from './../../tools/binary';
setReadOnly( ns, 'binary', binary );

/**
* @name binaryFactory
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/random/tools/binary-factory}
*/
import binaryFactory from './../../tools/binary-factory';
setReadOnly( ns, 'binaryFactory', binaryFactory );

/**
* @name unary
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/random/tools/unary}
*/
import unary from './../../tools/unary';
setReadOnly( ns, 'unary', unary );

/**
* @name unaryFactory
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/random/tools/unary-factory}
*/
import unaryFactory from './../../tools/unary-factory';
setReadOnly( ns, 'unaryFactory', unaryFactory );


// EXPORTS //

export default ns;
