'use strict';

// MODULES //

import factory from './factory.js';


// MAIN //

/**
* Fills a strided array with pseudorandom numbers drawn from a Poisson distribution.
*
* @name poisson
* @type {Function}
* @param {NonNegativeInteger} N - number of indexed elements
* @param {Collection} lambda - mean parameter
* @param {integer} sl - `lambda` stride length
* @param {Collection} out - output array
* @param {integer} so - `out` stride length
* @returns {Collection} output array
*
* @example
* import Float64Array from '@stdlib/array/float64';
*
* // Create an array:
* var out = new Float64Array( 10 );
*
* // Fill the array with pseudorandom numbers:
* poisson( out.length, [ 2.0 ], 0, out, 1 );
*/
var poisson = factory();


// EXPORTS //

export default poisson;
